import React from 'react'

import './Testimonials.css'

let testimonials = [
      {
            id: 1,
            name: 'John Doe',
            text: '"Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum, esse aliquam. Eligendi neque sint voluptates dicta quisquam praesentium culpa iure. Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum, esse aliquam. Eligendi neque sint voluptates dicta quisquam praesentium culpa iure."',
            imageUrl: "data:image/jpeg;base64,"
      },
      {
            id: 2,
            name: 'Mary Johnson',
            text: '"Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum, esse aliquam. Eligendi neque sint voluptates dicta quisquam praesentium culpa iure. Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum, esse aliquam. Eligendi neque sint voluptates dicta quisquam praesentium culpa iure."',
            imageUrl: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSZ0OTAqC4xVoXNE8eLYie4DDjlLgZZrwj2cB64su1Z9f5YuarNKHYM8WoOrdFxTqoYjVE&usqp=CAU"
      }
]


export const Testimonials = () => {
      return (
            <div id="testimonials" className="testimonials section">
                  <h2 className="section__title">Testimonios</h2>

                  <div className="container">
                        <div className="testimonials-container">
                              {
                                    testimonials.map(item => (
                                          <div className="testimonials-card">
                                                <img className="testimonials-card__img" src={item.imageUrl} alt={item.name} />

                                                <div className="testimonials-card__info">
                                                      <h2 className="testimonials-card__name">{item.name}</h2>
                                                      <p className="testimonials-card__text">{item.text}</p>
                                                      <a className="testimonials-card__link" href="#">Ver proceso</a>
                                                </div>
                                          </div>
                                    ))
                              }
                        </div>
                  </div>
            </div>
      )
}
